  text-decoration: none;
  cursor: pointer; }

p > a {
  font-weight: bold; }
  p > a:hover {
    text-decoration: underline; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

* {
  cursor: default;
  -webkit-user-select: none; }

input,
textarea {
  -webkit-user-select: text;
  cursor: text; }

form,
input,
optgroup,
select,
textarea {
  -webkit-user-select: text;
  -webkit-app-region: no-drag; }

.display-block {
  display: block;
  width: 100%; }

