  text-decoration: none;
  font-weight: normal;
  background-color: transparent;
  cursor: pointer;
  user-select: none; }
  .btn:active {
    outline: 0; }
  .btn.btn-primary {
    border: 1px solid #222;
    color: #222; }
    .btn.btn-primary:hover {
      background-color: #222;
      color: #fff; }
  .btn.btn-secondary {
